import React from 'react';
import './Introduction.css'
// import { Route, Routes } from 'react-router-dom';
// import { Outlet, Link } from "react-router-dom";
import { useDispatch, useSelector } from 'react-redux';
import {handleNavPageName} from '../../redux/slices/main/mainSlice'

function IntroductionComponent() {
  const dispatch = useDispatch()
  dispatch(handleNavPageName('introduction'))

  return (
    <div className='majorContainer'>
      {/*    */}
      <div className='subContainer'>
        {/* <div className='majorText'></div> */}
        <div className='introductionMajorText'>   </div>
        <div class='row'>
          {/*  */}
          <div class='col-md-6' style={{display:'flex',  flexDirection:'column', justifyContent:'space-evenly'}}>
            {/*   */}
            <div class='col-md-12'>
              <div style={{display:'flex', justifyContent:'center', alignItems:'center'}}>
                {/*  */}
                <div class='col-3 col-md-3'>
                  <img className='introTopImage' src='/image/introductionImage/portfolioMe2.jpg' alt="main logo"/>
                </div>
                {/*  */}
                <div class='col-9 col-md-9'>
                  <h1 className='introTopText'><br /></h1>
                  <h1 className='introTopText'>.</h1>
                </div>
              </div>
            </div>
            {/*   */}
            <div class='col-md-12'>
              <div style={{display:'flex', justifyContent:'center', alignItems:'center'}}>
                {/*  */}
                <div class='col-3 col-md-3'>
                  <h1 className='purpose'></h1>
                </div>
                {/*  */}
                <div class='col-9 col-md-9'>
                  <p className='purposeText'>  6  <br />, ,  <br />   .</p>
                </div>
              </div>
            </div>
          </div>
          {/*  */}
          <div class='col-md-6' style={{display: 'flex', flexDirection: 'column', justifyContent:'center'}}>
            {/*  */}
            <div className='introRightTop'>
              <p className='introRightTopText'>     ,<br/>     .</p>
            </div>
            {/*  */}
            <div className='introRightMiddle'>
              <div className='imageBorder'>
                <div className='imageBorderCircle'>
                  <img src='/image/introductionImage/working.png' alt="" className='valuesImage'/>
                </div>
                <p className='imageBorderCircleText'></p>
              </div>
              <div className='imageBorder'>
                <div className='imageBorderCircle'>
                  <img src='/image/introductionImage/accountability.png' alt="" className='valuesImage'/>
                </div>
                <p className='imageBorderCircleText'></p>
              </div>
              <div className='imageBorder'>
                <div className='imageBorderCircle'>
                  <img src='/image/introductionImage/communication.png' alt="" className='valuesImage'/>
                </div>
                <p className='imageBorderCircleText'></p>
              </div>
            </div>
            {/*  */}
            <div className='introRightBottom'>
              <p className='introRightBottomText' >
                       .<br/>
                    ,   .<br/>
                       .<br/>
                      .
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default IntroductionComponent;
